import React, { useEffect, useState } from 'react';

// Theme utility hook
export const useTheme = () => {
    const [theme, setTheme] = useState('light');

    useEffect(() => {
        const queryParameters = new URLSearchParams(window.location.search);
        const themeParam = queryParameters.get("theme");
        
        if (themeParam && ['dark', 'blue', 'gray', 'light'].includes(themeParam)) {
            setTheme(themeParam);
            console.log("Theme set to: " + themeParam);
        } else {
            setTheme('light');
            console.log("Theme set to: light (default)");
        }
    }, []);

    return theme;
};

// Theme provider component
export const ThemeProvider = ({ children }) => {
    const theme = useTheme();

    useEffect(() => {
        // Remove any existing theme classes
        document.body.classList.remove('theme-dark', 'theme-blue', 'theme-gray', 'theme-light');
        
        // Add the current theme class
        document.body.classList.add(`theme-${theme}`);
        
        // Also set a CSS custom property for the theme
        document.documentElement.style.setProperty('--current-theme', theme);
    }, [theme]);

    return (
        <div className={`theme-${theme}`}>
            {children}
        </div>
    );
};

export default ThemeProvider;
